import React from 'react';
import MusicMeasure from './MusicMeasure';
import MusicPart from './MusicPart';
import Glyph from '../components/MusicGlyph';
import {lineh, systemDistance, staffDistance} from '../config/MusicConfig';

// const nameOfFifths = [
// 	"C major", //0
//     "G major", //1
